import { Injectable } from '@angular/core';
import { ActivatedRouteSnapshot, Resolve, RouterStateSnapshot } from '@angular/router';
import { Observable } from 'rxjs';
import { RxLocalizationService } from './localization.service';

@Injectable({
  providedIn: 'root'
})
export class RxLoginLocalizationResolver implements Resolve<Observable<any>> {
  constructor(private rxLocalizationService: RxLocalizationService) {}

  resolve(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): Observable<any> {
    return this.rxLocalizationService.initLoginTranslations();
  }
}
